import React, { useContext } from "react";
import { NavContext } from "../context/navContext";
import { Profile } from "../components/Profile";
import { Button, Space, Modal } from "antd";
import Project from "../components/Project";
import { Footer } from "antd/es/layout/layout";
import WebPublisher from "../components/WebPublisher";

function Main() {
    const { openProfile, setOpenProfile } = useContext(NavContext);
    return (
        <div className="w-full h-full bg-mainColor">
            <div className="w-8/12 h-full pt-28  pb-12 mb-36 z-50 m-auto">
                <Project />
                <WebPublisher />

                <Footer />
                <Space wrap className="pb-8 pt-4 flex justify-end">
                    {/* <Button onClick={() => setOpenProfile(true)}>
                         Profile
                    </Button> */}
                    <Modal
                        title="  ."
                        centered
                        open={openProfile}
                        onCancel={() => setOpenProfile(false)}
                        width={1000}
                    >
                        <Profile />
                    </Modal>
                </Space>
            </div>
        </div>
    );
}

export default Main;
