import React, { useContext } from "react";
import { NavContext } from "../context/navContext";

import { Button } from "antd";
import img1 from "../assets/img/frame1.png";
import img3 from "../assets/img/frame3.png";
import img4 from "../assets/img/frame4.png";
import img5 from "../assets/img/frame5.png";
import img6 from "../assets/img/frame6.png";
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/effect-coverflow";
import "swiper/css/pagination";

import "./styles.css";

// import required modules
import { EffectCoverflow, Pagination } from "swiper";

function WebPublisher() {
    const navData = useContext(NavContext);
    return (
        <div ref={navData.webPublish}>
            <div className="w-full h-auto bg-white p-8 mt-4">
                <h2 className="tit">  </h2>
                <div className="titBox">
                    <p className="leading-normal	">
                        Parallax Scrolling     
                        . <br />
                           , , jS   
                        keyframe  CSS   .
                        <br />
                               
                        :)
                        <br />
                        <span className="text-stone-500	">
                            skill: html5, css3, jQuery
                        </span>
                    </p>
                </div>
                <div className="project">
                    <div className="w-full h-1/2">
                        {/* <img src={img1} alt="" /> */}
                        <Swiper
                            effect={"coverflow"}
                            grabCursor={true}
                            centeredSlides={true}
                            slidesPerView={"auto"}
                            initialSlide={2}
                            coverflowEffect={{
                                rotate: 50,
                                stretch: 0,
                                depth: 100,
                                modifier: 1,
                                slideShadows: true,
                            }}
                            pagination={true}
                            modules={[EffectCoverflow, Pagination]}
                            className="mySwiper"
                        >
                            <SwiperSlide>
                                <img src={img1} alt="portfolio image" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={img3} alt="portfolio image" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={img4} alt="portfolio image" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={img5} alt="portfolio image" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={img6} alt="portfolio image" />
                            </SwiperSlide>
                        </Swiper>
                    </div>
                </div>
                <a
                    href="https://dabin-lee.github.io/web-portfolio/"
                    target="_blank"
                >
                    <Button className="w-full mt-2">Launch</Button>
                </a>
            </div>
        </div>
    );
}

export default WebPublisher;
