import React, { useContext, useEffect, useState } from "react";
import { NavContext } from "../context/navContext";
export const Nav = () => {
    const navData = useContext(NavContext);
    const [scroll, setScroll] = useState(false);
    useEffect(() => {
        window.addEventListener("scroll", () => {
            if (window.scrollY > 50) {
                setScroll(true);
            } else {
                setScroll(false);
            }
        });
    }, []);
    const handleScroll = (ref) => {
        window.scrollTo({
            top: ref.offsetTop,
            left: 0,
            behavior: "smooth",
        });
    };
    return (
        <div
            className={`fixed top-0 h-16 w-full pr-4 py-1 mb-2 z-10 text-gray-600 border-b bg-slate-300 duration-700 ease-in ${
                scroll && "backdrop-blur-md bg-white/30"
            }`}
        >
            <div className="w-10/12 h-full m-auto flex items-center justify-end">
                <button
                    className="ml-5 blur-none"
                    onClick={() =>
                        window.scrollTo({ top: 0, behavior: "smooth" })
                    }
                >
                    Project
                </button>
                <button
                    className="ml-5 blur-none"
                    onClick={() => handleScroll(navData.webPublish.current)}
                >
                    Experience
                </button>
                <button
                    className="ml-5 blur-none"
                    onClick={() => navData.setOpenProfile(true)}
                >
                    <span className="text-teal-500">AboutMe</span>
                </button>
            </div>
        </div>
    );
};
